.navbar {
	.container {
		padding: 1em;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.logo {
			height: 70px;
			img {
				width: auto;
				height: 100%;
			}
		}
		.navbar-nav {
			font-family: Nunito, Helvetica, sans-serif;
			background-color: rgba(0, 10, 20, 0.7);
			min-height: 100vh;
			overflow: auto;
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			z-index: 10;
			padding: 20px;
			display: none;
			.nav-items {
				.nav-item {
					margin: 10px 0;
					a {
						background-color: #002030;
						border-radius: 0.5rem;
						font-size: 1.5rem;
						padding: 10px;
						color: whitesmoke;
						display: inline-block;
						text-align: center;
						width: 100%;
						height: 100%;
						&:hover {
							background-color: #224050;
						}
					}
				}
				.menu-close {
					color: white;
					font-size: 2rem;
					display: flex;
					justify-content: center;
				}
			}
			&.open {
				display: block;
			}
			&.close {
				display: none;
			}
		}
		.menu-btn {
			font-size: 2rem;
			cursor: pointer;
		}
	}
}

@media screen and (min-width: 670px) {
	.navbar {
		.container {
			.navbar-nav {
				background-color: unset;
				min-height: unset;
				position: unset;
				display: block;
				.nav-items {
					display: flex;
					.nav-item {
						margin: 0 5px;
						a {
							background-color: white;
							font-size: 1rem;
							border: none;
							border-radius: 1rem;
							box-shadow: 1px 1px 3px gray;
							color: inherit;
							padding: 5px 10px;
							font-size: 1.2rem;
							font-weight: 600;
							&:hover {
								background-color: unset;
							}
							&:active {
								transform: scale(95%);
								box-shadow: 0 0 4px lightgray;
							}
							&.active {
								background-color: whitesmoke;
								transform: scale(95%);
								box-shadow: 0 0 4px lightgray;
								cursor: default;
							}
						}
					}
					.menu-close {
						display: none;
					}
				}
			}
			.menu-btn {
				display: none;
			}
		}
	}
}
