.slider {
	overflow: hidden;
	width: 100%;
	margin: auto;
	border-radius: 1rem;

	.slider-container {
		position: relative;
		width: 300%;
		margin: 0;
		left: 0;
		animation: 15s slider infinite;

		figure {
			overflow: hidden;
			position: relative;
			float: left;
			width: 33.33%;

			figcaption {
				width: 100%;
				position: absolute;
				bottom: 0;
				background-color: rgb(0, 0, 0, 0.4);
				color: white;
				text-align: center;
				padding: 0.3em;
			}

			img {
				width: 100%;
				margin: auto;
			}
		}
	}
}

@keyframes slider {
	0% {
		left: 0;
	}
	25% {
		left: 0;
	}
	30% {
		left: -100%;
	}
	75% {
		left: -100%;
	}
	80% {
		left: -200%;
	}
	100% {
		left: -200%;
	}
}
