.container {
	max-width: 1100px;
	margin: auto;
	padding: 20px;
}

.text-center {
	text-align: center;
}

.construction-warning {
	background-color: rgba(255, 239, 214, 0.8);
	color: #333;
	border: 1px solid lightgray;
	border-radius: 5px;
	margin: 1em;
	padding: 0.3em 1em;
	position: fixed;
	bottom: 0;
	right: 0;
	display: none;
	transition: display 0.3s ease;
	span {
		font-size: 1.2rem;
		color: red;
		position: absolute;
		top: -14px;
		right: -14px;
	}
}

.desp {
	margin: auto;
	margin-bottom: 20px;
	padding: 1rem;
	border-radius: 0.5rem;
	box-shadow: 1px 1px 5px grey;
	max-width: 675px;
	background-color: white;
}
