.features {
	.container {
		h2 {
			text-align: center;
		}
		.card {
			width: 100%;
			margin: 1rem auto;
			background: white;
			border: 5px solid #f5f5f5;
			border-radius: 0.5rem;
			box-shadow: 1px 1px 5px grey;
			outline: 1px solid $text-color;
			text-align: center;
			padding: 15px;
			overflow: hidden;
			* {
				margin: 20px 0;
			}
			p {
				font-size: 15px;
			}
			.card-img {
				margin: auto;
				opacity: 0.8;
				width: 50%;
			}
		}
	}
}

@media screen and (min-width: 675px) {
	.banner {
		.container {
			display: flex;
			justify-content: space-around;
			align-items: center;
			text-align: center;
			text-align: left;
			.text-content {
				padding: 40px;
				h1 {
					font-size: 3em;
					text-align: left;
				}
			}

			.card {
				margin: 0;
			}
		}

		.home-slideshow {
			width: 800px;
		}
	}

	.features {
		.container {
			.cards {
				display: flex;
				justify-content: center;
				.card {
					flex-basis: 300px;
				}
			}
		}
	}
}
