footer {
	margin: 0;
	color: whitesmoke;
	h3 {
		margin-bottom: 1em;
	}
	a {
		color: whitesmoke;
		&:hover {
			color: #ccc;
		}
	}
	.footer-top {
		background-color: #283747;
		.container {
			.col {
				margin-bottom: 20px;
				padding: 10px;
			}
			li {
				margin-bottom: 10px;
			}
			.social-links {
				display: flex;
				gap: 0.5em;
				font-size: 1.5rem;
				margin-top: 0.5em;
			}
			.login {
				a {
					display: block;
					padding: 0.5em 1em;
					margin-bottom: 10px;
					border: none;
					border-radius: 2em;
					&.google {
						background-color: white;
						color: $text-color;
					}
					&.facebook {
						background-color: #3b5998;
					}
				}
			}
		}
	}
	.footer-bottom {
		background-color: #1b2631;
	}
}

@media screen and (min-width: 670px) {
	footer {
		.footer-top {
			.container {
				display: flex;
				justify-content: space-between;
				li {
					margin-bottom: 10px;
				}
			}
		}
	}
}
